import { Link } from "react-router-dom";

export default function Table(){
    return <>
   
    <div className="card col-8">
    
            <div className="card-body">
    <table class="table">
  <thead class="thead-dark">
    <tr>
      <th scope="col">Id</th>
      <th scope="col">First</th>
      <th scope="col">Last</th>
      <th scope="col">Edit</th>
      <th scope="col">Delete</th>
    </tr>
  </thead>
  <tbody>
    <tr>
      <th scope="row">1</th>
      <td>Mark</td>
      <td>Otto</td>
      <td><Link to="/edittable/1">Edit</Link></td>
      <td><button className="btn btn-secondary">Delete</button></td>
    </tr>
    <tr>
      <th scope="row">2</th>
      <td>Jacob</td>
      <td>Thornton</td>
      <td><Link to="/edittable/2">Edit</Link></td>
      <td><button className="btn btn-secondary">Delete</button></td>
    </tr>
    <tr>
      <th scope="row">3</th>
      <td>Larry</td>
      <td>the Bird</td>
      <td><Link to="/edittable/3">Edit</Link></td>
      <td><button className="btn btn-secondary" >Delete</button></td>
    </tr>
  </tbody>
</table>
</div>
</div>
    </>
}